@import "components/variables";
@import "components/mixin";
@import "components/header";
@import "components/accordeon";
@import "components/howToMakeOrder";
@import "components/addres";
@import "components/deliveryPopup";
@import "components/fixed_nav";
@import "components/authorization";
@import "components/aside_buttons";

body {
  .header{
    position: unset!important;
  }
  .content {
    padding-top: 25px;
    width: 100%;
    .containerParent();
    .container {
      .containerChild();
      display: grid;
      grid-template-areas: "aside breadcrumb breadcrumb"
                           "aside productImg descr"
                           "aside analogue analogue"
                           "recomend recomend recomend"
                           "order order order";
      grid-template-columns: 25% 1fr 30%;
      .content__productCard__breadcrumb {
        grid-area: breadcrumb;
        ul {
          display: flex;
          flex-wrap: wrap;
          li {
            a {
              display: inline-block;
              font: .875em/1em @sansregular;
              color: #474747;
            }
            span {
              font: .875em/1em @sansregular;
              color: #474747;
            }
          }
        }
      }
      .content__productCard__img{
        grid-area: productImg;
        h5{
          font: 2.25em/2.375em @serifitalic;
          color: @linkColor;
        }
        ul{
          display: flex;
          flex-wrap: wrap;
          li{
            padding: 30px 15px;
            width: 90%;
            border: 2px solid #e6e6e6;
            border-radius: 15px;
            &:hover{
              cursor: pointer;
            }
          }
        }
        .slideshow{
          li{
            height: 400px;
            display: flex!important;
            justify-content: center;
            align-items: center;
            z-index: 0!important;
            img{
              width: 100%;
            }
          }
        }
        .slideshowNav{
          li{
            margin: 20px 20px 0 0;
            width: 80px;
            height: 35px;
            display: flex!important;
            justify-content: center;
            align-items: center;
            img{
              width: 100%;
            }
          }
        }
      }
      .content__productCard__descr{
        grid-area: descr;
        margin: 85px 0 0 0;
        display: flex;
        flex-direction: column;
        .packaging,
        .plate{
          margin: 10px 0;
          padding: 25px;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          align-items: center;
          border: 2px solid #e6e6e6;
          border-radius: 15px;
          .value{
            margin-bottom: 25px;
            width: 100%;
            font: 1em/1.125em @sansregular;
            color: @linkColor;
            i{
              color: @mainColor;
            }
          }
          .price{
            width: 30%;
            font: 1.5em/1.625em @sansbold;
            color: @linkColor;
            span{
              font: .625em/.75em @sansregular;
            }
          }
          button{
            width: 125px;
            height: 40px;
            font: .875em/40px @sansregular;
            color: #fff;
            text-align: center;
            text-transform: uppercase;
            background-color: @mainColor;
            border: none;
            border-radius: 5px;
            outline: none;
            &:hover{
              cursor: pointer;
            }
          }
          .packagingQuantitu,
          .plateQuantitu{
            display: flex;
            align-items: center;
            span{
              display: inline-block;
              width: 25px;
              height: 40px;
              font: 1.375em/40px @sansregular;
              text-align: center;
              color: @linkColor;
              background-color: #77c9cc;
              user-select: none;
              &:hover{
                background-color: @mainColor;
                cursor: pointer;
                transition: .5s;
              }
            }
            .minus{
              border-top-left-radius: 5px;
              border-bottom-left-radius: 5px;
            }
            .plus{
              border-top-right-radius: 5px;
              border-bottom-right-radius: 5px;
            }
            input{
              width: 35px;
              height: 34px;
              font-size: 1em;
              font-family: @sansregular;
              text-align: center;
              color: @linkColor;
              border: 2px solid #77c9cc;
              outline: none;
            }
          }
        }
        .others{
          margin: 10px 0;
          width: 75%;
          height: 60px;
          position: relative;
          border: 2px solid #e6e6e6;
          border-radius: 15px;
          p{
            display: flex;
            justify-content: center;
            align-items: center;
            font: 1em/60px @sansbold;
            color: @mainColor;
            border-radius: 15px;
            &:hover{
              color: #fff!important;
              background-color: @mainColor!important;
              cursor: pointer;
              transition: .5s;
            }
            i{
              font-size: 1.5em;
            }
          }
          .othersFormOfSale{
            position: absolute;
            z-index: 2;
            background-color: #fff;
            border-radius: 15px;
            box-shadow: 5px 5px 10px #e6e6e6;
            .capsules,
            .box{
              padding: 15px;
              display: flex;
              flex-wrap: wrap;
              justify-content: center;
              align-items: center;
              border: 2px solid #e6e6e6;
              .value{
                margin-bottom: 15px;
                width: 100%;
                display: flex;
                justify-content: flex-start;
                font: 1em/1.125em @sansregular;
                color: @linkColor;
                i{
                  color: @mainColor;
                }
                &:hover{
                  color: unset!important;
                  background-color: unset!important;
                  cursor: unset!important;
                }
              }
              .price{
                width: 50%;
                display: flex;
                align-items: baseline;
                font: 1.5em/1.625em @sansbold;
                color: @linkColor;
                span{
                  font: .625em/.75em @sansregular;
                }
                &:hover{
                  color: unset!important;
                  background-color: unset!important;
                  cursor: unset!important;
                }
              }
              .capsulesQuantitu,
              .boxQuantitu{
                width: 50%;
                display: flex;
                justify-content: center;
                align-items: center;
                span{
                  display: inline-block;
                  width: 25px;
                  height: 40px;
                  font: 1.375em/40px @sansregular;
                  text-align: center;
                  color: @linkColor;
                  background-color: #77c9cc;
                  user-select: none;
                  &:hover{
                    background-color: @mainColor;
                    cursor: pointer;
                    transition: .5s;
                  }
                }
                .minus{
                  border-top-left-radius: 5px;
                  border-bottom-left-radius: 5px;
                }
                .plus{
                  border-top-right-radius: 5px;
                  border-bottom-right-radius: 5px;
                }
                input{
                  width: 35px;
                  height: 34px;
                  font-size: 1em;
                  font-family: @sansregular;
                  text-align: center;
                  color: @linkColor;
                  border: 2px solid #77c9cc;
                  outline: none;
                }
              }
              button{
                margin: 15px 0 0;
                width: 125px;
                height: 40px;
                font: .875em/40px @sansregular;
                color: #fff;
                text-align: center;
                text-transform: uppercase;
                background-color: @mainColor;
                border: none;
                border-radius: 5px;
                outline: none;
                &:hover{
                  cursor: pointer;
                }
              }
            }
            .box{
              border-bottom-left-radius: 15px;
              border-bottom-right-radius: 15px;
            }
          }
        }
        .productInfo{
          display: flex;
          div{
            display: flex;
            flex-direction: column;
            p{
              margin: 10px 0;
              font: 1em/1.125em @sansbold;
              color: @linkColor;
            }
            span{
              margin: 10px 0;
              font: 1em/1.125em @sansregular;
              color: @linkColor;
            }
          }
        }
        .consultation{
          a{
            margin-top: 35px;
            padding: 5px;
            width: 275px;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            font: 1.125em/1.1em @sansregular;
            color: #474747;
            text-decoration: none;
            border: 1px solid @mainColor;
            border-radius: 5px;
            i{
              margin: 0 20px 0 0;
              padding: 0;
              font-size: 2em;
              color: #7d3daf;
              border-radius: 5px;
            }
          }
        }
      }
      .content__productCard__analogue{
        grid-area: analogue;
        margin-top: 40px;
        .productCard__descr{
          display: flex;
          li{
            margin: 0 5px 0 0;
            width: 130px;
            text-align: center;
            border: 1px solid #e6e6e6;
            border-top-left-radius: 15px;
            border-top-right-radius: 15px;
            a{
              width: 100%;
              display: inline-block;
              font: 1.125em/50px @sansbold;
              color: @linkColor;
              text-decoration: none;
              &:focus{
                outline: none;
              }
            }
          }
        }
        .ui-tabs-active {
          .ui-tabs-anchor {
            width: 100%;
            background-color: #77c9cc;
            border-top-left-radius: 15px;
            border-top-right-radius: 15px;
            outline: none;
          }
        }
        .therapeuticEffect,
        .farmCare,
        .manual,
        .videoSurveillance{
          padding: 30px 20px;
          border: 1px solid #e6e6e6;
          border-radius: 0 5px 5px 5px;
          .completeAnalogue,
          .otherReleseForm,
          .similarEffect{
            display: flex;
            flex-wrap: wrap;
            h6{
              margin: 30px 10px 20px;
              width: 35%;
              font: 1.125em/1.25em @sansbold;
              color: @mainColor;
            }
            span{
              margin: 30px 0 20px;
              width: 60%;
              font: .875em/1em @sansregular;
              color: #252525;
              text-align: right;
            }
            ul{
              display: flex;
              flex-wrap: wrap;
              li {
                margin: 10px 10px;
                position: relative;
                text-align: center;
                border: 1px solid #e6e6e6;
                border-radius: 5px;
                a {
                  display: block;
                  text-decoration: none;
                  img {
                    margin-top: 15px;
                    width: 200px;
                    height: 200px;
                  }
                  p {
                    padding: 10px;
                    font: 1em/1.125em @sansregular;
                    color: @linkColor;
                    text-align: left;
                  }
                  .price {
                    font: 1.25em/1.375em @sansbold;
                    span {
                      font: .875em/1em @sansregular
                    }
                  }
                }
                .hover{
                  padding: 0;
                  margin: 0;
                  width: 100%;
                  height: 100%;
                  display: none;
                  position: absolute;
                  top: 0;
                  z-index: 2;
                  border: 1px solid #77c9cc;
                  border-radius: 5px;
                  box-shadow: 5px 5px 10px #e6e6e6;
                  .link{
                    display: block;
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    background-color: rgba(119, 201, 204, .3);
                  }
                  .addBusket,
                  .addFavorite{
                    display: block;
                    width: 75%;
                    height: 40px;
                    position: relative;
                    z-index: 1;
                    font: .875em/40px @sansregular;
                    text-transform: uppercase;
                  }
                  .addBusket{
                    margin: 0 0 40px;
                    color: #fff;
                    background-color: @mainColor;
                    border-radius: 5px;
                  }
                  .addFavorite{
                    margin: 0 0 10px;
                    color: @linkColor;
                    background-color: #fff;
                    border: 1px solid @linkColor;
                    box-sizing: border-box;
                    border-radius: 5px;
                  }
                }
                &:hover {
                  border-color: #176975;
                  cursor: pointer;
                  transition: .5s;
                  .hover{
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: center;
                    align-items: flex-end;
                    cursor: default;
                  }
                }
              }
            }
          }
        }
      }
      .content__recomend{
        grid-area: recomend;
        margin: 25px 0 25px 0;
        h5 {
          font: 2.25em/2.375em @serifitalic;
          color: @linkColor;
        }
        .slider {
          width: 100%;
          height: 350px;
          padding: 0 30px;
          display: flex;
          flex-wrap: wrap;
          justify-content: center;
          position: relative;
          ul {
            li {
              margin: 0 15px;
              width: 225px;
              height: 350px;
              position: relative;
              text-align: center;
              border: 1px solid #e6e6e6;
              border-radius: 5px;
              box-sizing: border-box;
              a {
                display: block;
                text-decoration: none;
                img {
                  margin-top: 15px;
                  width: 200px;
                  height: 200px;
                }
                p {
                  padding: 10px;
                  font: 1em/1.125em @sansregular;
                  color: @linkColor;
                  text-align: left;
                }
                .price {
                  font: 1.25em/1.375em @sansbold;
                  span {
                    font: .875em/1em @sansregular
                  }
                }
              }
              .hover{
                padding: 0;
                margin: 0;
                width: 223px;
                height: 350px;
                display: none;
                position: absolute;
                top: 0;
                z-index: 2;
                border: 1px solid #77c9cc;
                border-radius: 5px;
                box-shadow: 5px 5px 10px #e6e6e6;
                .link{
                  display: block;
                  position: absolute;
                  width: 100%;
                  height: 100%;
                  background-color: rgba(119, 201, 204, .3);
                }
                .addBusket,
                .addFavorite{
                  display: block;
                  width: 75%;
                  height: 40px;
                  position: relative;
                  z-index: 1;
                  font: .875em/40px @sansregular;
                  text-transform: uppercase;
                }
                .addBusket{
                  margin: 0 0 40px;
                  color: #fff;
                  background-color: @mainColor;
                  border-radius: 5px;
                }
                .addFavorite{
                  margin: 0 0 10px;
                  color: @linkColor;
                  background-color: #fff;
                  border: 1px solid @linkColor;
                  box-sizing: border-box;
                  border-radius: 5px;
                }
              }
              &:hover {
                border-color: @fontColor;
                cursor: pointer;
                transition: .5s;
                .hover{
                  display: flex;
                  flex-wrap: wrap;
                  justify-content: center;
                  align-items: flex-end;
                  cursor: default;
                }
              }
            }
          }
          .center {
            a {
              position: absolute;
              top: 40%;
              height: 100px;
              text-align: center;
              text-decoration: none;
              z-index: 5;
              border: 1px solid #e6e6e6;
              border-radius: 10px;
              background-image: none;
              i{
                font-size: 2em;
                line-height: 100px;
                color: @fontColor;
              }
              &:hover{
                border-color: @fontColor;
                transition: .5s;
              }
            }
            .prevRecomend{
              left: 10px;
            }
            .nextRecomend{
              right: 10px;
            }
          }
        }
      }
      .content__howToMakeOrder {
        grid-area: order;
      }
    }
  }
  ::-webkit-scrollbar{
    width: 10px;
  }
  ::-webkit-scrollbar-button{
    width: 10px;
    height: 0;
  }
  ::-webkit-scrollbar-thumb{
    width: 5px;
    border: 1px solid @linkColor;
    border-radius: 5px;
    background-color: @fontColor;
    box-shadow: 0 1px 1px #fff inset;
  }
  ::-webkit-scrollbar-track{
    width: 5px;
    background-color: @mainColor;
  }
  ::-webkit-scrollbar-track-piece{
    box-shadow: 0 1px 1px #000 inset;
  }
}